.main-nav {
  margin: 0 auto;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  display: none;
}

.nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: $c-main;
  text-align: center;
}

.nav-list__item {
  width: 100%;
  padding: 20px 0;

  border-bottom: 1px solid $c-main;

  box-shadow: 0 1px 0 $basic-black,
    inset 0 2px 0 $special-white;
}

.nav-list__link {
  font-family: $ff-main;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  color: $basic-white;
  text-decoration: none;
  text-transform: uppercase;
}

@media (min-width: $desktop-width) {
  .main-nav {
    display: block;
  }

  .nav-list {
    width: 640px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
    top: -80px;
    right: 140px;
    background-color: transparent;
  }

  .nav-list__item {
    border: none;
    box-shadow: none;
  }

  .nav-list__link {
    font-weight: 400;
    line-height: 18px;
    text-transform: none;
  }
}
